import { client } from '@/shared/AppConfig'
import { 
    Header as AppHeader, 
    SidebarToggler, 
    Sidebar as AppSidebar, 
    SidebarFooter, 
    SidebarForm, 
    SidebarHeader, 
    SidebarMinimizer, 
    SidebarNav, 
    Aside as AppAside, 
    AsideToggler, 
    Footer as TheFooter, 
    Breadcrumb 
} from '@coreui/vue'
import AdminAside from './admin-aside'
import AdminHeaderDropdown from './admin-header-dropdown'
import * as menu from '@/_nav'
import * as roles from '@/shared/Roles'
export default {
    name: 'admin',
    data () {
        return {
            brandImageFull: require('@/assets/img/lucas.png'),
            brandImageMinimized: require('@/assets/img/vipana-min.png'),
            brandName: '',
            client,
            nav: [],
            role: this.$store.state.authModule.role
        }
    },
    components: {
        AdminAside,
        AdminHeaderDropdown,
        AsideToggler,
        AppHeader,
        AppSidebar,
        AppAside,
        TheFooter,
        Breadcrumb,
        SidebarForm,
        SidebarFooter,
        SidebarToggler,
        SidebarHeader,
        SidebarNav,
        SidebarMinimizer
    },
    computed: {
        name () {
            return this.$route.name
        },
        list () {
            return this.$route.matched.filter((route) => route.meta.label || route.name)
        }
    },
    methods: {
        handleLogout() {
            this.$bvModal.show('modalLogout')
        },
        handleModalLogoutOk() {
            this.$store.dispatch('logout')
            this.$router.push('/login')
        },
        setMenu() {
            this.role == roles.ADMIN ? this.nav = menu.adminMenu : 
            (this.role == roles.DMP ? this.nav = menu.dmpMenu : 
            (this.role == roles.LAWYER ? this.nav = menu.lawyerMenu : this.nav = []))
        },
        toProfileView() {
            this.$router.push('/admin/profile')
        }
    },
    created() {
        this.setMenu()
    }
}